          <div class="property-desc">v1.3.2 true</div>
        </div>
        <div class="property-item">
          <div class="property-name">showDownLoadBtn</div>
          <div class="property-type">boolean</div>
          <div class="property-desc">v1.3.2 true</div>
        </div>
      </div>

      <h3 class="sub-section-title"></h3>
      <div class="property-card">
        <div class="property-item">
          <div class="property-name">fileLoaded</div>
          <div class="property-type">function</div>
          <div class="property-desc">
             
            <label class="note">iframewindow.addEventListener('message', (event) => {})</label>
          </div>
        </div>
      </div>

      <h3 class="sub-section-title"></h3>
      <div class="notes-card">
        <ul class="note-list">
          <li></li>
          <li>
            vue-cli
            <code>vue.config.js</code>
            
            <code>publicPath: './'</code>
             
          </li>
          <li>
            officeofficePDFPDF
