          @click="activeTab = index"
        >
          {{ tab.title }}
        </div>
      </div>
      
      <div class="tab-content">
        <!-- Tab 1: iframe -->
        <div v-show="activeTab === 0" class="tab-pane">
          <div class="usage-card">
            <h3 class="sub-section-title"></h3>
            <div class="usage-desc">
              <p>
                
                <a
                  href="https://github.com/zyl-ui/vue-file-viewer/tree/master/public/file-viewer"
                  target="_blank"
                  class="link"
                >
                  file-viewer
                </a>
                iframe
              </p>
              <p></p>
            </div>

            <h3 class="sub-section-title"></h3>
            <ol class="step-list">
              <li>
                
                <a
                  href="https://github.com/zyl-ui/vue-file-viewer/tree/master/public/file-viewer"
                  target="_blank"
                  class="link"
                >
                  file-viewer
                </a>
                
              </li>
              <li>
                 file-viewer
                  vue-cli
                 public 
              </li>
            </ol>

            <h3 class="sub-section-title"></h3>
            <div class="example-container">
              <div class="example-preview">
                <iframe
                  src="./file-viewer/index.html?fileUrl=https://file-viewer.me7.cn/fileTest/pdf.pdf"
                  scrolling="auto"
